// "use client";
import { useEffect } from "react";

/**
 * Features:
 * - data-animate: "up" | "fade" | "scale"
 * - data-delay: per-item delay in ms
 * - data-stagger on a parent: step in ms for its direct children
 */
export default function useInView(selector = "[data-animate]", options) {
  useEffect(() => {
    try {
      if (typeof window === "undefined" || typeof document === "undefined")
        return;
      if (!("IntersectionObserver" in window)) return;

      // 1) Prepare staggered parents
      const parents = document.querySelectorAll("[data-stagger]");
      parents.forEach((parent) => {
        const step = parseInt(parent.getAttribute("data-stagger") || "100", 10);
        // :scope is well supported in modern Chrome/Edge/Firefox; fallback if needed
        const kids = parent.querySelectorAll(":scope > *") || parent.children;
        Array.from(kids).forEach((el, i) => {
          if (!el.getAttribute("data-animate"))
            el.setAttribute("data-animate", "up");
          if (!el.getAttribute("data-delay"))
            el.setAttribute("data-delay", String(i * step));
        });
      });

      // 2) Observe all animatable elements
      const els = document.querySelectorAll(selector);
      if (!els.length) return;

      const map = {
        up: "anim-up",
        fade: "anim-fade-in",
        scale: "anim-scale-in",
      };

      const obs = new IntersectionObserver(
        (entries) => {
          entries.forEach((e) => {
            if (!e.isIntersecting) return;
            const el = e.target;
            const mode = el.getAttribute("data-animate") || "up";
            const delay = parseInt(el.getAttribute("data-delay") || "0", 10);
            if (delay > 0) el.style.animationDelay = `${delay}ms`;
            el.classList.add("is-inview", map[mode] || "anim-up");
            el.classList.remove("will-animate");
            obs.unobserve(el);
          });
        },
        { threshold: 0.15, rootMargin: "0px 0px -6% 0px", ...(options || {}) }
      );

      els.forEach((el) => {
        el.classList.add("will-animate");
        obs.observe(el);
      });

      return () => obs.disconnect();
    } catch {
      // swallow to avoid crashing navigation
    }
  }, [selector]); // keep deps simple/stable
}
